.toast {
  position: fixed;
  border-radius: 4px;
  left: 50%;
  top: 50%;
  padding: 10px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99;
  opacity: 1;
  -webkit-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.text {
  color: #fff;
  font-size: 14px;
}
.hide {
  opacity: 0;
}



