.alert {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.4);
}
.confirm {
  width: 230px;
  border-radius: 10px;
  background-color: #fff;
  -webkit-transform: translate3d(0, -20px, 0);
          transform: translate3d(0, -20px, 0);
  opacity: 0;
  -webkit-transition: opacity 0.2s, -webkit-transform 0.2s;
  transition: opacity 0.2s, -webkit-transform 0.2s;
  -o-transition: opacity 0.2s, transform 0.2s;
  transition: opacity 0.2s, transform 0.2s;
  transition: opacity 0.2s, transform 0.2s, -webkit-transform 0.2s;
}
.confirm.active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.content {
  padding: 16px 14px;
  text-align: center;
}
.content .title {
  font-size: 16px;
  font-weight: bold;
}
.content .subtitle {
  margin-top: 10px;
  font-size: 12px;
}
.btnsContent {
  display: -ms-flexbox;
  display: flex;
}
.btnsContent .btn {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  font-size: 16px;
  padding: 10px;
}
.btnsContent .btn:last-child::after {
  display: none;
}



