.button {
  display: block;
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-touch-callout: none;
}
.button:active {
  opacity: 0.6;
}
.disabled {
  opacity: 0.3;
}



